const MIXINS_BARGAINS_AND_TEAM_BUY_APIINSERTMERACTIVITY = {
  methods: {
    // 新增商家活动
    apiInsertMerActivity () {
      const data = this.form
      data.rpl = this.goodsAndActivity
      data.activeType = this.activeType
      data.type = 1 // 0-平台 1-商家
      return this.$http.post('/woho/activity/manage/insertMerActivity', data).then((res) => res)
    },
    // 更新商家活动
    apiUpdateMerActivity () {
      let data = this.form
      data.rpl = [...this.goodsAndActivity, ...this.oldGoodsAndActivity]
      data = JSON.parse(JSON.stringify(data).replace(/originalPrice/g, 'price'))
      data.activeType = this.activeType
      data.type = 1 // 0-平台 1-商家
      return this.$http.post('/woho/activity/manage/updateMerActivity', data).then((res) => res)
    },
    // 获取已关联的商品
    apiFindById() {
      this.$http.post(`/woho/activity/manage/findByActivityIdGetProduct/${this.$route.query.id}`)
        .then((res) => {
          this.form = res.data
          this.oldGoodsAndActivity = res.data.rpl
        })
    }
  }
}

