<template>
  <div>
    <LayoutBox one="分类管理">
      <el-form :inline="true" size="small">
        <el-form-item :model="formSearch">
          <div class="header_box">
            <el-button class="inquire" icon="el-icon-plus" type="success" size="small" @click="onAdd">新增分类</el-button>
          </div>
        </el-form-item>
        <el-form-item label="分类名称">
          <el-input v-model="formSearch.categoryName"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSearch">查询</el-button>
        </el-form-item>
      </el-form>
      <el-table :data="sortList" height="530">
        <el-table-column prop="categoryName" label="分类名称" align="center"></el-table-column>
        <el-table-column label="级别" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.level === 1 ? '一级' : scope.row.level === 2 ? '二级' : '三级' }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="rank" label="排序" align="center"></el-table-column>
        <el-table-column prop="createTime" label="创建日期" align="center"></el-table-column>
        <el-table-column label="操作" align="center">
          <template slot-scope="scope">
            <el-button style="color: #5bd9be" type="text" size="mini" @click="onUpdate(scope)">修改</el-button>
            <el-button style="color: #e52424" type="text" size="mini" @click="onDelete(scope)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <el-pagination background layout="prev, pager, next" :total="total" @current-change="handleCurrentChange"></el-pagination>
    </LayoutBox>
    <el-dialog :title="id ? '编辑商品分类' : '新增商品分类'" :visible.sync="dialogVisible" style="min-width: 1000px">
      <el-form ref="form" :model="form" :rules="rules" size="mini" label-width="120px">
        <el-form-item label="分类名称：" prop="categoryName">
          <el-input v-model="form.categoryName"></el-input>
        </el-form-item>
        <el-form-item label="级别：" prop="level">
          <el-select v-model="form.level" placeholder="请选择" @change="onLevel">
            <el-option label="一级" :value="1" />
            <el-option label="二级" :value="2" />
            <el-option label="三级" :value="3" />
          </el-select>
        </el-form-item>
        <el-form-item v-if="form.level === 2 || form.level === 3" label="父级分类：" required>
          <el-select v-model="parentId" placeholder="请选择" @change="onParentId">
            <el-option v-for="item in parentLevelList" :key="item.id" :label="item.categoryName" :value="item.id" />
          </el-select>
        </el-form-item>
        <el-form-item v-if="form.level === 1" label="图标：" required>
          <el-upload ref="upload" action="http-request" :http-request="onUpload" list-type="picture-card" :on-remove="onRemove" :file-list="imageUrlList" :limit="1">
            <i class="el-icon-plus"></i>
          </el-upload>
        </el-form-item>
        <el-form-item label="排序：" prop="rank">
          <el-input v-model.number="form.rank"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button v-if="id" class="inquire1" type="success" style="width: 80px" @click="onSave('form')">保存</el-button>
          <el-button v-else class="inquire1" type="success" style="width: 80px" @click="onSubmit('form')">提交</el-button>
          <el-button style="width: 80px" @click="dialogVisible = false">取消</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import LayoutBox from '@/components/LayoutBox/index'
import { MIXINS_GETSMALLPARENTCATEGORY_API, MIXINS_APIFINDBYIDADVERTISING_API, MIXINS_DELETEADVERTISING_API,
  MIXINS_UPDATECATEGORY_API
} from '@/mixins/api.js'

export default {
  name: 'SortList',

  components: {
    LayoutBox
  },

  mixins: [
    MIXINS_GETSMALLPARENTCATEGORY_API,
    MIXINS_APIFINDBYIDADVERTISING_API,
    MIXINS_DELETEADVERTISING_API,
    MIXINS_UPDATECATEGORY_API,
  ],

  data () {
    return {
      id: '', // 用来修改的主键id
      sortList: [], // 分类分页
      pageNo: 1, // 分页当前页
      total: 0, // 总条数
      imageUrlList: [], // 图标
      dialogVisible: false, // 开启添加分类的弹窗
      parentLevelList: [], // 分类父级列表
      parentId: '', // 父ID
      formSearch: {}, // 搜索
      form: {
        categoryName: '', // 分类名称
        log: '', // 图片
        level: '', // 级别
        parentId: '', // 父ID
        rank: '', // 排序
        platformType: 1, // 平台类型(0-平台1-小超)
      },
      rules: {
        categoryName: [
          { required: true, message: '请输入分类名称', trigger: 'blur' }
        ],
        rank: [
          { required: true, message: '请输入分类排序', trigger: 'blur' }
        ],
        level: [
          { required: true, message: '请选择级别', trigger: 'change' }
        ]
      }
    }
  },

  created () {
    this.apiGetSortList()
  },

  methods: {
    // 点击提交
    onSubmit (formName) {
      const data = this._formRules(formName)
      if (data) {
        this.apiInsertAdvertising(data)
      }
    },
    // 编辑时点击保存
    onSave (formName) {
      const data = this._formRules(formName)
      if (data) {
        data.id = this.id
        this.apiUpdateAdvertising(data)
      }
    },
    // 点击新增
    onAdd () {
      this.id = ''
      this.form = {}
      this.imageUrlList = []
      this.dialogVisible = true
    },
    // 点击修改
    onUpdate (scope) {
      let data = {
        id: scope.row.id
      }
      this.apiFindByIdAdvertising(data).then((res) => {
        if (res.status === 200) {
          this.form = res.data
          return this._parentLevel(this.form.level - 1)
        } else {
          this.$message.error(res.statusText)
        }
      }).then((res) => {
        if (res.data.success) {
          this.parentLevelList = res.data.data
          this.parentId = this.form.parentId
          this.imageUrlList = [{ url: this.form.log }]
          this.id = this.form.id
          this.dialogVisible = true
        } else {
          this.$message.error(res.data.msg)
        }
      })
    },
    // 点击删除
    onDelete (scope) {
      this.apiDeleteAdvertising(scope)
    },
    // 移除图片时的钩子
    onRemove (file, fileList) {
      this.imageUrlList = []
    },
    // 自定义覆盖图片上传方法
    onUpload (params) {
      const file = params.file
      const isJPG = file.type === 'image/jpeg' || file.type === 'image/png'
      if (!isJPG) {
        this.$message.error('上传的图标只能是 JPG/PNG 格式!')
        this.imageUrlList = []
        return
      }
      const reader = new FileReader()
      reader.readAsDataURL(file)
      reader.onload = (e) => {
        const img = new Image()
        img.src = e.target.result
        if (img.complete) {
        // 如果存在浏览器缓存中
          if (!this._uploadValidate(img)) {
            return
          }
          this.apiUpload(file)
        } else {
          img.onload = () => {
            if (!this._uploadValidate(img)) {
              return
            }
            this.apiUpload(file)
          }
        }
      }
    },
    // 分页
    handleCurrentChange (pageNo) {
      this.pageNo = pageNo
      this.apiGetSortList()
    },
    // 选择级别
    onLevel (e) {
      if (e === 1) {
        this.form.parentId = 0
      } else {
        this._parentLevel(e - 1).then((res) => {
          if (res.data.success) {
            this.parentLevelList = res.data.data
            this.form.parentId = ''
            this.parentId = ''
          } else {
            this.$message.error(res.data.msg)
          }
        })
      }
    },
    // 选择父级分类
    onParentId (e) {
      this.form.parentId = e
    },
    // 点击查询
    onSearch () {
      this.apiGetSortList()
    },

    // 私有方法-----------------------------------------------------------
    // 图标的验证
    _uploadValidate (img) {
      if (img.width !== img.height) {
        this.$message.error('图标必须是正方形或者是圆形')
        this.imageUrlList = []
        return false
      }
      if (img.width > 150 || img.width < 100) {
        this.$message.error('图标尺寸不能大于150*150或者小于100*100')
        this.imageUrlList = []
        return false
      }
      return true
    },
    // 表单验证
    _formRules (formName) {
      let data = ''
      if (this.form.parentId === '0') {
        if (this.imageUrlList.length < 1) {
          this.$message.error('请选择图标')
          return false
        }
        this.form.log = this.imageUrlList[0].url
      }
      this.$refs[formName].validate((valid) => {
        if (valid) {
          data = this.form
        } else {
          data = false
        }
      })
      return data
    },

    // 添加分类时获取的父级分类
    _parentLevel (level) {
      let data = {
        level: level,
        pageSize: 999,
      }
      return this.getSmallParentCategory(data).then((res) => res)
    },

    // api---------------------------------------------------------------------------------------
    // 图片上传
    apiUpload (file) {
      const formData = new FormData()
      formData.append('file', file)
      this.$http.post('/common/oss/uploadFile', formData)
        .then((res) => {
          if (res.data.success) {
            // eslint-disable-next-line no-unused-vars
            const imageUrlList = this.form.imageUrlList
            this.imageUrlList = [{ url: res.data.data.webUrl }]
          } else {
            this.$message.error(res.data.msg)
          }
        })
    },
    // 更新分类区分小超和平台
    apiUpdateAdvertising (data) {
      this.updateCategory(data).then((res) => {
        if (res.data.success) {
          this.$message.success('添加成功')
          this.dialogVisible = false
          this.apiGetSortList()
        }
      })
    },
    // 添加分类
    apiInsertAdvertising (data) {
      this.$http.post('/SmallProductCategory/addCategory', data)
        .then((res) => {
          if (res.data.success) {
            this.$message.success('添加成功')
            this.dialogVisible = false
            this.apiGetSortList()
          } else {
            this.$message.error(res.data.msg)
          }
        })
    },
    // 获得分类列表
    apiGetSortList () {
      let data = {
        pageNo: this.pageNo,
        ...this.formSearch
      }
      this.getSmallParentCategory(data).then((res) => {
        if (res.data.success) {
          this.sortList = res.data.data
          this.total = res.data.data[0].count
        } else {
          this.$message.error(res.data.msg)
        }
      })
    },
    // 删除
    apiDeleteAdvertising (scope) {
      this.deleteAdvertising(scope.row.id).then((res) => {
        if (res.data.success) {
          this.$message.success('删除成功')
          this.sortList.splice(scope.$index, 1)
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
  .header_box{
    display: flex;
    justify-content: space-between;
    .el_alert{
      width: 70%;
    }
  }
</style>
